a:focus.FloatingButton {
  outline: 0;
  border: none;
  -moz-outline-style: none; }

a:active.FloatingButton {
  outline: 0;
  border: none;
  -moz-outline-style: none; }

.FloatingButton {
  height: 50px;
  width: 50px;
  background: #2fb454;
  border-style: solid;
  border-width: 0px;
  color: #ffffff;
  border-color: #000000;
  border-radius: 25px;
  cursor: pointer;
  line-height: 50px;
  font-size: 25px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  outline: none;
  -webkit-box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
  /*z-index: 1000;*/ }
  .FloatingButton > img {
    width: 100%; }

.icon:nth-child(5) {
  -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
          transform: scaleX(-1); }

.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-shadow: 4px 4px rgba(0, 0, 0, 0.1); }

.icon-holder {
  width: 50px;
  height: 50px;
  position: relative; }

.menu-text {
  opacity: 0;
  font-size: 0.5em;
  display: block;
  position: absolute;
  bottom: 0px;
  line-height: 0.5em;
  left: 50%;
  -webkit-transform: translate(-50%, 200%);
      -ms-transform: translate(-50%, 200%);
          transform: translate(-50%, 200%);
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  -webkit-transition-timing-function: ease-in-out;
       -o-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out; }

.FloatingButton:hover .menu-text {
  opacity: 1; }



